/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

ul, li {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.8rem;
  line-height: 1.4;
  position: relative;
}

p {
  color: #000000;
  font-size: 2rem;
}

a {
  text-decoration: none;
  margin: 0;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  margin: 0;
}

h1, h2 {
  font-weight: 700;
}

h3, h4 {
  font-weight: 400;
}

.wrapper {
  width: 90%;
  margin: 0 auto;
}

.header__overlay {
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, hsla(153, 98%, 36%, 0.4) 28.91%, transparent 100%);
  position: absolute;
  z-index: -1;
}
.header__overlay__video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  mix-blend-mode: overlay;
}
.header__overlay__img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  mix-blend-mode: overlay;
  z-index: -1;
}
.header__overlay__title {
  position: absolute;
  bottom: 12%;
  left: 7%;
  text-transform: uppercase;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 5rem;
  z-index: -1;
}
.header__index {
  height: 70vh;
  position: relative;
}
.header__index::after {
  content: url("../img/logo.webp");
  width: 100%;
  max-width: 30rem;
  position: absolute;
  top: 15%;
  left: 0;
  transform: translateX(41vw);
}
.header__pages {
  background-color: #02B665;
}
.header__sections {
  position: relative;
  min-height: 42rem;
}
.header__nav {
  z-index: 1;
}
.header__nav ul {
  display: flex;
  list-style: none;
  justify-content: space-around;
  height: 8.1rem;
  align-items: center;
}
.header__nav a {
  color: white;
  text-decoration: none;
  font-size: 2.2rem;
}
.header__nav--btn {
  background-color: #000000;
  color: #FFFFFF;
  border-radius: 1rem;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  font-weight: 700;
  transition: all 0.3s ease;
}
.header__nav--btn:hover {
  background-color: #000000;
}
.header__nav--btn:active {
  background-color: #000000;
}
.header__nav--btn:disabled {
  background-color: #ACACAC;
}

.footer {
  background-color: #000000;
  height: 59rem;
  color: #FFFFFF;
}
.footer .footer__button {
  background-color: #039E00;
  color: #FFFFFF;
}
.footer .footer__button:hover {
  background-color: #039E00;
}
.footer .footer__button:active {
  background-color: #039E00;
}
.footer .footer__button:disabled {
  background-color: #ACACAC;
}

.index-menu {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  gap: 2rem;
  margin: auto;
  justify-content: center;
  list-style: none;
}
.index-menu__btn {
  background-color: #02B665;
  color: #FFFFFF;
  border-radius: 3.5rem;
  padding: 3rem 3rem 2rem 3rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: flex-start;
  align-items: center;
  font-weight: 700;
  transition: all 0.3s ease;
  height: 100%;
  width: 100%;
  min-height: 20rem;
  max-width: 20rem;
  min-width: 20rem;
  overflow-wrap: break-word;
  transform: translate(0, -50%);
}
.index-menu__btn:hover {
  background-color: #02B665;
}
.index-menu__btn:active {
  background-color: #02B665;
}
.index-menu__btn:disabled {
  background-color: #ACACAC;
}
.index-menu__icon {
  width: 6rem;
  height: 6rem;
}
.index-menu__text {
  font-size: 1.4rem;
  color: #FFFFFF;
}

.home-main__title {
  font-size: 4rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 5rem;
  color: #000000;
}
.home-main__div {
  display: flex;
  flex-wrap: wrap;
  column-gap: 15rem;
  row-gap: 5rem;
  margin: auto;
  justify-content: center;
  margin-bottom: 5rem;
}
.home-main__card {
  background-color: #FFFFFF;
  border-radius: 3.5rem;
  box-shadow: 1rem 0.9rem 1.4rem rgba(172, 161, 161, 0.5);
  padding: 3rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: flex-start;
  align-items: center;
  transition: all 0.3s ease;
  height: 100%;
  min-width: 36rem;
  max-width: 36rem;
}
.home-main__card:hover {
  transform: scale(1.1);
}
.home-main__card__icon {
  width: 8rem;
}
.home-main__card__title {
  font-size: 1.8rem;
  font-weight: 700;
  color: #02B665;
  text-transform: uppercase;
}
.home-main__card__list {
  list-style-image: url("../img/icono-check.webp");
}

.home-banner {
  height: 35rem;
  background-image: url("../img/banner.webp");
  background-size: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  margin: 10rem 0;
  display: grid;
  place-items: center;
}
.home-banner__title {
  font-size: 4rem;
  font-weight: 700;
  text-align: center;
  color: #FFFFFF;
  font-size: 2.6rem;
  padding: 0 17%;
}

.home-services__title {
  color: #1A4715;
  font-size: 4rem;
  text-align: center;
  font-weight: 700;
}
.home-services__subtitle {
  color: #02B665;
  font-size: 3.6rem;
  text-align: center;
}
.home-services__grid {
  display: grid;
  margin: 10rem 0;
  gap: 5rem;
  grid-template-columns: repeat(3, auto);
}
.home-services__card, .home-services--other__card {
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 1rem 0.9rem 1.4rem rgba(172, 161, 161, 0.5);
  border-color: #02B665;
  border-width: 0.2rem;
  border-style: solid;
}
.home-services__text {
  border-top-color: #02B665;
  border-top-width: 0.2rem;
  border-top-style: solid;
  color: #1A4715;
  text-transform: capitalize;
  margin: 0;
  padding: 1rem 1.5rem;
  text-align: center;
}
.home-services__image {
  min-width: 38rem;
  height: 40rem;
  object-fit: cover;
}
.home-services--other {
  background-color: #ECFFF6;
}
.home-services--other__flex {
  display: flex;
  flex-wrap: wrap;
  column-gap: 15rem;
  row-gap: 5rem;
  margin: auto;
  justify-content: center;
  margin-bottom: 5rem;
}
.home-services--other__card {
  flex: 1;
  flex-basis: 0;
  min-width: 25%;
  max-width: 30rem;
}